@import url('https://fonts.googleapis.com/css?family=Pacifico');
.img{
  height: 600px;
  left: 0;
  z-index: -300;
}

ul li{
  text-decoration: none;
}

.content {
  position: fixed;
  top: 90%;
  right: -150px;
  -webkit-transform: translate(-70%, -70%);
          -ms-transform: translate(-70%, -70%);
      transform: translate(-70%, -70%);
  height: 160px;
  overflow: hidden;
  font-family: 'Lato', sans-serif;
  font-size: 35px;
  line-height: 40px;
  color: rgba(173, 154, 49, 0.87);
}

.main-header{
  z-index: 500;
}

.content__container {
  font-weight: 1000;
  overflow: hidden;
  height: 40px;
  padding: 0 50px;
  margin-top: 80px;
}
.content__container:before {
  content: '[';
  left: 5%;
}
.content__container:after {
  content: ']';
  position: absolute;
  right: 5%;
}

.diana , .maria{
  margin-top: -390px !important;
}

.titledianamaria{
  margin-left: -18% !important;
  color: black !important;
  text-shadow: 2px 2px 2px white !important;
}

.content__container:after, .content__container:before {
  position: absolute;
  top: 80px;
  color: #D89F35;
  font-size: 42px;
  line-height: 40px;
  -webkit-animation-name: opacity;
  -webkit-animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  animation-name: opacity;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}
.content__container__text {
  display: inline;
  float: left;
  margin: 0;
}
.content__container__list {
  margin-top: 0;
  padding-left: 200px;
  text-align: left;
  list-style: none;
  -webkit-animation-name: change;
  -webkit-animation-duration: 10s;
  -webkit-animation-iteration-count: infinite;
  animation-name: change;
  animation-duration: 10s;
  animation-iteration-count: infinite;
}

.photopic{
  margin-top: -20px !important;
  width: 100% !important;
  height: 600px;
}

.content__container__list__item {
  line-height: 40px;
  margin-top: 0;
}

@-webkit-keyframes opacity {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
@-webkit-keyframes change {
  0%, 12.66%, 100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  16.66%, 29.32% {
    -webkit-transform: translate3d(0, -25%, 0);
            transform: translate3d(0, -25%, 0);
  }
  33.32%,45.98% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
  }
  49.98%,62.64% {
    -webkit-transform: translate3d(0, -75%, 0);
            transform: translate3d(0, -75%, 0);
  }
  66.64%,79.3% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
  }
  83.3%,95.96% {
    -webkit-transform: translate3d(0, -25%, 0);
            transform: translate3d(0, -25%, 0);
  }
}
@keyframes opacity {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
@keyframes change {
  0%, 12.66%, 100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  16.66%, 29.32% {
    -webkit-transform: translate3d(0, -25%, 0);
            transform: translate3d(0, -25%, 0);
  }
  33.32%,45.98% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
  }
  49.98%,62.64% {
    -webkit-transform: translate3d(0, -75%, 0);
            transform: translate3d(0, -75%, 0);
  }
  66.64%,79.3% {
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
  }
  83.3%,95.96% {
    -webkit-transform: translate3d(0, -25%, 0);
            transform: translate3d(0, -25%, 0);
  }
}

@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700,300italic,400italic,700italic);
.header {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  height: 100%;
}
.header__dev {
  color: #0A0A07;
  font-family: 'Pacifico', cursive;
  text-shadow: 0 1px 1px rgba(91, 93, 116, 0.5);
  font-size: 86px;
  height: 90px;
  line-height: 90px;
  font-weight: 400;
  position: absolute;
  top: 50%;
  left: 120px;
  margin: -45px 0 0;
  -webkit-transition: all 0.9s ease-out;
  -webkit-transition: all 0.6s ease-out;
  -o-transition: all 0.6s ease-out;
  transition: all 0.6s ease-out;
  -webkit-transition-delay: 0.8s;
          -o-transition-delay: 0.9s;
     transition-delay: 0.9s;

}
@media screen and (max-width: 1000px) {
  .header__dev {
    -webkit-transform: scale(0.9);
            -ms-transform: scale(0.9);
        transform: scale(0.9);
  }
}
@media screen and (max-width: 850px) {
  .header__dev {
    -webkit-transform: scale(0.65);
            -ms-transform: scale(0.65);
        transform: scale(0.65);
  }
}
@media screen and (max-width: 700px) {
  .header__dev {
    -webkit-transform: scale(0.5);
            -ms-transform: scale(0.5);
        transform: scale(0.5);
  }
}

.dev__slash {
  color: rgb(128, 128, 128);
  font-weight: 700;
  position: absolute;
  top: -70%;
  left: 95px;
  margin-left: 20px;
  -webkit-transform: scale(4.4);
          -ms-transform: scale(4.4);
      transform: scale(4.4);
  z-index: 5;
}
.dev__fe, .dev__ux {
  /* background: rgba(255, 204, 0, 0.32); */
  top: 100%;
  margin-top: -90px;
  position: absolute;
  width: 270px;
  height: 180px;
  overflow: hidden;
  -webkit-transform: rotateZ(24deg);
          -ms-transform: rotate(24deg);
      transform: rotateZ(24deg);
}
.dev__fe i, .dev__ux i {
  font-style: normal;
  top: 0;
  left: 0;
  position: absolute;
  width: 200px;
  height: 100px;
  line-height: 180px;
  -webkit-transform: rotateZ(-24deg);
          -ms-transform: rotate(-24deg);
      transform: rotateZ(-24deg);
}
.dev__fe {
  text-align: right;
  direction: rtl;
  margin-left: -40px;
}
.dev__fe i {
  text-indent: -28px;
}
.dev__ux {
  margin-left: 205px;
}
.dev__ux i {
  text-indent: -10px;
}
.dev__developer {
  font-weight: 500;
  position: absolute;
  top: 160%;
  left: 255px;
  background: rgba(255, 132, 0, 0.77);
}

.dev__fe, .dev__ux, .dev__fe i, .dev__ux i, .dev__developer, .dev__slash {
  -webkit-transition: all 0.6s cubic-bezier(0, -0.375, 0, 1.615);
  -o-transition: all 0.6s cubic-bezier(0, -0.375, 0, 1.615);
  transition: all 0.6s cubic-bezier(0, -0.375, 0, 1.615);
}

.header__dev--open .dev__fe i, .header__dev--open .dev__ux i {
  text-indent: 3px;
}
.header__dev--open .dev__developer {
  -webkit-transform: translateX(56px);
          -ms-transform: translateX(56px);
      transform: translateX(56px);
  opacity: 0.2;
}
.header__dev--open .dev__slash {
  -webkit-transform: scale(1.6) rotateZ(-1deg);
          -ms-transform: scale(1.6) rotate(-1deg);
      transform: scale(1.6) rotateZ(-1deg);
}

.header__dev--slow .dev__fe, .header__dev--slow .dev__ux, .header__dev--slow .dev__fe i, .header__dev--slow .dev__ux i, .header__dev--slow .dev__developer, .header__dev--slow .dev__slash {
  -webkit-transition: all 1.2s cubic-bezier(0, -0.375, 0, 1.615);
  -webkit-transition: all 3.2s cubic-bezier(0, -0.375, 0, 1.615);
  -o-transition: all 3.2s cubic-bezier(0, -0.375, 0, 1.615);
  transition: all 3.2s cubic-bezier(0, -0.375, 0, 1.615);
}



