@import url('https://fonts.googleapis.com/css?family=Open+Sans');
@import url('https://fonts.googleapis.com/css?family=Pacifico');
@import url('https://fonts.googleapis.com/css?family=Lato');
.headernav{
  vertical-align: middle;
}
.btns{
  position: relative;
  top:0;
  right: 0;
  width: 100%;
  padding: 5px 0px 5px 5px;
  font-family: 'Open Sans', sans-serif;
  z-index: 100;
  background: rgba(0, 0, 0);
}

.first-btn, .second-btn, .third-btn, .fourth-btn{
  color:rgba(173, 154, 49, 0.87);
  font-family: 'Open Sans', sans-serif;
  padding: 40px;
}

@import url(http://weloveiconfonts.com/api/?family=entypo);

/* entypo */
[class*="entypo-"]:before {
  font-family: 'entypo', sans-serif;
}

::-moz-selection {
background: transparent;
}

::selection {
background: transparent;
}

#content {
  text-align:center;
  
  position:absolute;
  left:0;
  right:0;
  margin:auto;
  padding:120px 0 10px 0;
  
  width:200px;
  height:20px;
}

input[type="checkbox"]{display:none;}

.checkbox:checked + .label{
  background:#7B7484;
  color:#231733;
}

.checkbox:checked ~ .social {
  opacity:1;
  -webkit-transform: scale(1) translateY(-90px);
  -ms-transform:scale(1) translateY(-90px);
      transform:scale(1) translateY(-90px);
    -webkit-transition:.5s;
  -o-transition:.5s;
  transition:.5s;
}

.label {
  font-size:16px;
  cursor:pointer;
  margin:0;
  padding:5px 10px;
  
  border-radius:10%;
  color:#7B7484;
}

.social {
  -webkit-transform-origin:50% 0%;
      -ms-transform-origin:50% 0%;
          transform-origin:50% 0%;
  
  -webkit-transform: scale(0) translateY(-190px);
  -ms-transform:scale(0) translateY(-190px);
      transform:scale(0) translateY(-190px);
  
  opacity:0;
}
.ul {
  position:relative;
  left:0;
  right:0;
  margin:auto;
  color:#fff;
  height:46px;
  width:180px;
  background:#030303;
  padding:0;
  list-style:none;
}

.ul li {
  font-size:20px;
  
  cursor:pointer;
  width:60px;
  margin:0;
  padding:12px 0;
  text-align:center;
  float:left;
  display:block;
  height:22px;}

.ul li:hover {color:rgba(173, 155, 49, 0.5);}

.ul:after {
  content:'';
  display:block;
  position:absolute;
  left:0;
  right:0;
  margin:35px auto;
  height:0;
  width:0;
  
  border-left: 20px solid transparent;
	border-right: 20px solid transparent;
	
	border-top: 20px solid #121213;
}

li[class*="twitter"] {padding:12px 0;}
li[class*="gplus"] {padding:12px 0;}

.imgport{
  margin-top: -150px;
  margin-left: 0px;
  background: lightgrey; 
  height: 600%; 
  width: 100%;
  filter: grayscale(100%) !important;
 -webkit-filter: grayscale(100%) !important;
 -webkit-transition:-webkit-filter 0.4s;
 transition:-webkit-filter 0.4s;
 -o-transition:filter 0.4s;
 transition:filter 0.4s;
 transition:filter 0.4s, -webkit-filter 0.4s;
 z-index: -100;
 text-shadow: 2px 2px 2px white !important;
}

.imgport:hover{
  filter: grayscale(0) !important;
  -webkit-filter: grayscale(0) !important;
  
}

.first-btn, .second-btn, .third-btn, .fourth-btn{
  padding: 20px 0px 20px 0;
}

.custom-btn {
  padding: 10px 25px;
  font-weight: 1000;
  font-family: 'Lato', sans-serif;
  font-size: 25px;
  background: transparent;
  outline: none !important;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
}


.btn-6 {
  line-height: 42px;
  padding: 0;
  border: none;
}

.navbar-nav>li>a {
  padding-top: 10px;
  padding-bottom: 10px;
  line-height: 20px;
  padding-right: -50px;
  margin-right: -25%;
  padding-top: 30px;
  margin-top: -10px;
}


