a {
  color: inherit;
  text-decoration: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}
::-moz-selection {
  background-color: #f7ca18;
  color: #1b1b1b;
}
::selection {
  background-color: #f7ca18;
  color: #1b1b1b;
}
nav {
  width: 100%;
  background-color: #0b0b0b;
  position: fixed;
  bottom: 0;
  height: 100px;
  overflow: hidden;
}
nav ul {
  position: absolute;
  top: 0;
  left: 0;
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 140vw;
}
nav li {
  margin: 0;
  float: left;
  display: inline-block;
  height: 98px;
  margin-bottom: 2px;
  line-height: 100px;
  width: 20vw;
  text-align: center;
  color: #555;
  -webkit-transition: background-color 0.5s 0.2s ease, color 0.3s ease;
  -o-transition: background-color 0.5s 0.2s ease, color 0.3s ease;
  transition: background-color 0.5s 0.2s ease, color 0.3s ease;
  cursor: pointer;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 800;
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
}
nav li.active {
  background-color: #151515;
  color: #efefef;
}
nav li.active::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #f7ca18;
}
nav li:nth-child(1) {
  -webkit-animation: pop 0.5s 0.15s 1 forwards;
          animation: pop 0.5s 0.15s 1 forwards;
}
nav li:nth-child(2) {
  -webkit-animation: pop 0.5s 0.3s 1 forwards;
          animation: pop 0.5s 0.3s 1 forwards;
}
nav li:nth-child(3) {
  -webkit-animation: pop 0.5s 0.45s 1 forwards;
          animation: pop 0.5s 0.45s 1 forwards;
}
nav li:nth-child(4) {
  -webkit-animation: pop 0.5s 0.6s 1 forwards;
          animation: pop 0.5s 0.6s 1 forwards;
}
nav li:nth-child(5) {
  -webkit-animation: pop 0.5s 0.75s 1 forwards;
          animation: pop 0.5s 0.75s 1 forwards;
}
nav li:nth-child(6) {
  -webkit-animation: pop 0.5s 0.9s 1 forwards;
          animation: pop 0.5s 0.9s 1 forwards;
}
nav li:nth-child(7) {
  -webkit-animation: pop 0.5s 1.05s 1 forwards;
          animation: pop 0.5s 1.05s 1 forwards;
}
nav li:hover {
  color: #ececec;
}
nav li:active {
  background-color: #222;
}
nav li:nth-child(1):hover ~ .slide {
  left: 0;
}
nav li:nth-child(2):hover ~ .slide {
  left: 20vw;
}
nav li:nth-child(3):hover ~ .slide {
  left: 40vw;
}
nav li:nth-child(4):hover ~ .slide {
  left: 60vw;
}
nav li:nth-child(5):hover ~ .slide {
  left: 80vw;
}
nav li:nth-child(6):hover ~ .slide {
  left: 100vw;
}
nav li:nth-child(7):hover ~ .slide {
  left: 120vw;
}
nav li.slide {
  position: absolute;
  left: -20vw;
  top: 0;
  background-color: #fff;
  z-index: -1;
  height: 2px;
  margin-top: 98px;
  -webkit-transition: left 0.3s ease;
  -o-transition: left 0.3s ease;
  transition: left 0.3s ease;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}
section {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  margin-top: 10%;
}
section .title {
  max-width: 60%;
  width: 100%;
  -ms-flex-item-align: center;
      align-self: center;
  -webkit-transform: translateY(-50px);
      -ms-transform: translateY(-50px);
          transform: translateY(-50px);
  margin: 0 auto;
  overflow: hidden;
  padding-bottom: 10px;
}
section .title span {
  display: inline-block;
  font-size: 5vw;
  color: #000000;
  width: 100%;
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-animation: byBottom 1s ease both;
          animation: byBottom 1s ease both;
  font-weight: 600;
}

.titleaboutme{
  text-transform: uppercase;
}

section .title span:last-child {
  font-size: 1rem;
  -webkit-animation: byBottom 1s 0.25s ease both;
          animation: byBottom 1s 0.25s ease both;
}
section .title span .a1 {
  position: absolute;
  margin-left: 1rem;
  text-decoration: none;
  color: #f7ca18;
  font-size: 20px;
  top: 15px;
  left: 95px;
}

section .title span .iphone5 {
  position: absolute;
  margin-left: 1rem;
  text-decoration: none;
  color: #f7ca18;
  font-size: 15px;
  top: 20px;
  left: 95px;
}

section .title span .a2 {
  position: relative;
  margin-left: 1rem;
  text-decoration: none;
  color: #f7ca18;
  font-size: 20px;
}
section .title span .a1::after, section .title span .a1::after , section .title span .iphone5::after {
  content: "";
  height: 6px;
  letter-spacing: 1px;
  background-color: #f7ca18;
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 0;
  -webkit-animation: linkAfter 0.5s 1s ease both;
          animation: linkAfter 0.5s 1s ease both;
}

.section h1{
  color: light gray;
}
@-webkit-keyframes pop {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes pop {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@-webkit-keyframes byBottom {
  0% {
    -webkit-transform: translateY(150%);
            transform: translateY(150%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes byBottom {
  0% {
    -webkit-transform: translateY(150%);
            transform: translateY(150%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@-webkit-keyframes linkAfter {
  0% {
    width: 0;
  }
  100% {
    width: 30px;
  }
}
@keyframes linkAfter {
  0% {
    width: 0;
  }
  100% {
    width: 30px;
  }
}

.imgaboutme{
  height: 790px;
  width: 100%;
  position: absolute;
  margin-top: -130px;
}



