.wrap{
  max-width: 960px;
  margin: 0 auto;
  position: relative;
  padding: 0;
}
.section{
  width:100%;
}
.section-title h1{
    margin: 0;
    line-height: 50px;
    letter-spacing: 5px;
    font-weight: 600;
    color: white;
}.border{
  display: block;
  height: 10px;
  margin-bottom: 40px;
  width: 100px;
  background: #91791B;
}
.row{
  width: 100%;
  min-height: 1px;
  float: left;
  position: relative;
  display: inline-block;
}.row .ull {
  margin: 0 20px 0 0 !important;
  text-align: left !important;
  padding: 0;
}

.skills{
  margin-top: 5%;
  width: 30%;
  border-radius: 50%;
}

.row .ull .div1{
  .div1st-style: none !important;
  
}.row .ull .div1 em{
  display: block;
  margin-bottom: 15px;
  margin-left: 20%;
}.row .ull .div1 div{
  margin-bottom: 35px;
  margin-left: 20%; 
  border-radius: 3px 3px 3px 3px;
  -webkit-border-radius: 3px 3px 3px 3px;
  background: #09131B;
  height: 42px;
  overflow: hidden;
}.row .ull .div1 div span{
  height: 42px;
  width: 50%;
  display: block;
  border-radius: 3px 3px 3px 3px;
  -webkit-border-radius: 3px 3px 3px 3px;
}

.skillstitle{
  margin-left: 40px;
  font-size: 40px;
}

.skillsdiv{
  margin-top: 0%;
}

.skillsdiv1{
  margin-top:5%;
}
.row .ull .div1.html div span { background: #91791B; width: 80%; }
.row .ull .div1.css div span { background: #91791B; width: 87%; }
.row .ull .div1.php div span { background: #91791B; width: 72%; }
.row .ull .div1.mysql div span { background: #91791B; width: 55%; }
.row .ull .div1.jquery div span { background: #91791B; width: 90%; }
.row .ull .div1.javascript div span { background: #91791B }
.row .ull .div1.seo div span { background: #91791B; width: 15%; }
.row .ull .div1.unix div span { width: 25%; background: #91791B; }


.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}.fadeInLeft {
  -webkit-animation-name: fadeInLeft;
  animation-name: fadeInLeft;
}
@-webkit-keyframes fadeInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
@keyframes fadeInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
    -ms-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }100% {
    opacity: 1;
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
}

.card, .card__heading {
  -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
          box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.card:after {
  content: '';
  display: table;
  clear: both;
}

body {
  background: #eff6f8;
  font-family: Roboto, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 18px;
}

.container {
  width: 100%;
  margin: 40px auto;
}

.card {
  display: block;
  width: 100%;
  position: relative;
  background: white;
  z-index: 0;
}
.card__heading {
  display: inline-block;
  position: absolute;
  top: -0.5em;
  left: -0.5em;
  padding: 14px 22px 11px;
  text-transform: uppercase;
  font-size: 2em;
  font-weight: 500;
  color: white;
  background: #A79A26;
}

.contact__map {
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
}
.contact__map:before, .contact__map:after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.contact__map:after {
  background: -webkit-radial-gradient(ellipse 80px 80px at 184px 155px, rgba(255, 255, 255, 0.2) 99%, #ffffff 250%);
  background: -o-radial-gradient(ellipse 80px 80px at 184px 155px, rgba(255, 255, 255, 0.2) 99%, #ffffff 250%);
  background: radial-gradient(ellipse 80px 80px at 184px 155px, rgba(255, 255, 255, 0.2) 99%, #ffffff 250%);
  mix-blend-mode: saturation;
}
.contact__map:before {
  background: -webkit-radial-gradient(ellipse 80px 80px at 184px 155px, rgba(255, 255, 255, 0.2) 99%, rgba(255, 255, 255, 0.8) 100%);
  background: -o-radial-gradient(ellipse 80px 80px at 184px 155px, rgba(255, 255, 255, 0.2) 99%, rgba(255, 255, 255, 0.8) 100%);
  background: radial-gradient(ellipse 80px 80px at 184px 155px, rgba(255, 255, 255, 0.2) 99%, rgba(255, 255, 255, 0.8) 100%);
  z-index: 1;
}
.contact__map .map__inner {
  width: 100%;
  height: 100%;
}
.contact__map .map__inner .leaflet-control-attribution {
  opacity: 0.4;
}
.contact__map .map__inner .leaflet-control-attribution:hover {
  opacity: 0.6;
}

.contact__content {
  float: right;
  padding: 30px 50px;
  line-height: 1.35;
}

dt {
  margin-top: 1.35em;
}
dt:first-child {
  margin-top: 0;
}

dt {
  color: #818181;
}

dd {
  color: #A79A26;
}




