.nav-holder, .nav-holder-h {
  background-color: #fff;
  color: #372B2F;
  display: inline-block;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 16px;
  margin: 10px 5px;
  max-width: 315px;
  min-width: 230px;
  overflow: hidden;
  position: relative;
  text-align: left;
  width: 100%;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}

.nav-holder *, .nav-holder-h *, .nav-holder *:before, .nav-holder-h *:before, .nav-holder-h *:after, .nav-holder *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.45s ease;
  -o-transition: all 0.45s ease;
  transition: all 0.45s ease;
}

.nav-holder:before, .nav-holder-h:before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  content: '';
  background-color: #ffffff;
  opacity: 0.5;
  -webkit-transition: all 0.45s ease;
  -o-transition: all 0.45s ease;
  transition: all 0.45s ease;
}

.nav-holder:before {
  -webkit-transform: translateY(-70%);
  -ms-transform: translateY(-70%);
      transform: translateY(-70%);
}

.nav-holder-h:before {
  -webkit-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
      transform: translateX(-100%);
}

.nav-holder:hover:before {
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
      transform: translateY(0);
  opacity: 0.8;
  -webkit-transition-delay: 0.1s;
  -o-transition-delay: 0.1s;
     transition-delay: 0.1s;
}

.nav-holder-h:hover:before {
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
      transform: translateX(0);
  opacity: 0.8;
  -webkit-transition-dely: 0.2s;
  -webkit-transition-delay: 0.2s;
       -o-transition-delay: 0.2s;
          transition-delay: 0.2s;
}

