  -o-transition-delay: 0.35s;
     transition-delay: 0.35s;
}
figure.snip1229 .heading {
  overflow: hidden;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
      transform: translateY(-50%);
}
figure.snip1229 h3 {
  display: table;
  margin: 0 auto;
  padding: 0 10px;
