  bottom: 9%;
  -webkit-transition-delay: 0s;
  -o-transition-delay: 0s;
     transition-delay: 0s;
}
figure.snip1229:hover p,
figure.snip1229.hover p {
  opacity: 1;
  -webkit-transition-delay: 0.4s;
  -o-transition-delay: 0.4s;
     transition-delay: 0.4s;
}

